/* Sidebar Base Styles */
.sidebar-main-conteiner {
  width: 250px;
  height: 100vh;
  background-color: #f8f9fa; /* light background */
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.1); /* soft shadow */
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  padding-top: 20px;
  font-family: 'Poppins', sans-serif;
}

/* Sidebar Heading */
.sidebar-heading {
  font-size: 1.5rem;
  font-weight: bold;
  padding: 10px 5px ;
  text-align: center;
  color: #0d6efd;
  letter-spacing: 1px;
}

/* Sidebar Links */
.sidebar-main-conteiner ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar-main-conteiner ul a {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 15px 20px;
  text-decoration: none;
  color: #555;
  font-weight: 500;
  transition: all 0.3s ease;
  border-radius: 8px;
}

.sidebar-main-conteiner ul a:hover {
  background-color: #e7f1ff;
  color: #0d6efd;
}

.sidebar-main-conteiner ul a.active {
  background-color: #d0e7ff;
  color: #0d6efd;
  font-weight: bold;
}

/* Icon Size */
.icon-size {
  width: 24px;
  height: 24px;
}

/* User Info Box */
.user-box-main {
  margin-top: auto;
  padding: 20px;
  background-color: #f1f3f5;
  display: flex;
  align-items: center;
  border-top: 1px solid #ddd;
}

.user-img-box img {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: 2px solid #6c757d;
}

.sidebar-user-detail-box {
  margin-left: 12px;
  display: flex;
  flex-direction: column;
}

.sidebar-user-detail-userName {
  font-weight: 600;
  font-size: 0.95rem;
  color: #6c757d;
}

.sidebar-user-detail-userEmail {
  font-size: 0.8rem;
  color: #6c757d;
}

/* ========== MOBILE STYLES ========== */
@media (max-width: 768px) {
  .sidebar-main-conteiner {
    width: 100vw;
    height: 60px;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    position: fixed;
    top: auto;
    bottom: 0;
    left: 0;
    border-right: none;
    border-top: 1px solid #ddd;
    background-color: #fff;
    overflow-y: hidden;
    overflow-x: auto;
    padding: 0;
  }

  .sidebar-main-conteiner::-webkit-scrollbar {
    display: none;
  }

  .sidebar-heading {
    display: none;
  }

  .sidebar-main-conteiner ul {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-around;
    margin: 0;
  }

  .sidebar-main-conteiner ul a {
    width: 13vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 0.75rem;
    color: #333;
    padding: 8px 0;
  }

  .sidebar-main-conteiner ul span {
    width: 13vh;
    text-align: center;
    font-size: 10px;
    margin-top: 3px;
  }

  .icon-size {
    width: 20px;
    height: 20px;
  }

  .user-box-main,
  .sidebar-user-detail-userName,
  .sidebar-user-detail-userEmail {
    display: none;
  }
}

/* Toast Unavailable Link */
.unavailable-link {
  display: flex;
  align-items: center;
  padding: 10px;
  cursor: pointer;
  text-decoration: none;
  color: inherit;
}

.unavailable-link:hover {
  background-color: #f5f5f5;
  border-radius: 10px;
}

/* Active Link */
.active {
  background-color: #d2f1ff;
  border-left: 5px solid #2196f3;
  color: #2196f3;
  border-radius: 10px;
}
