/* Dashboard.css */
.dashboard {
    padding: 2rem;
    /* background: #f2f2f2; Light grey background */
    min-height: 100dvh;
    margin-bottom: 70px;
  }
  
  .dashboard h2 {
    text-align: center;
    margin-bottom: 2rem;
    font-size: 2.5rem;
    color: #fff;
    font-weight: bold;
  }
  
  .dashboard-boxes {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
  }
  
  /* Common Card Style */
  .dashboard-box {
    padding: 2rem;
    border-radius: 20px;
    color: #fff;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
    font-weight: 500;
  }
  
  .dashboard-box:hover {
    transform: translateY(-8px);
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.25);
  }
  
  /* Each Box Different Color */
  .dashboard-box:nth-child(1) {
    background: linear-gradient(135deg, #00c6ff, #0072ff); /* Blue shades */
  }
  
  .dashboard-box:nth-child(2) {
    background: linear-gradient(135deg, #ff758c, #ff7eb3); /* Pink shades */
  }
  
  .dashboard-box:nth-child(3) {
    background: linear-gradient(135deg, #43e97b, #38f9d7); /* Green shades */
  }
  
  .dashboard-box:nth-child(4) {
    background: linear-gradient(135deg, #793d68, #a6c1ee); /* Purple/blue mix */
  }
  
  .dashboard-box-header th {
    font-size: 1.8rem;
    text-align: center;
    padding-bottom: 1rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  }
  
  .dashboard-invoice-box,
  .dashboard-value-box {
    margin-top: 1.5rem;
    font-size: 1.2rem;
  }
  
  .dashboard-invoice-box th,
  .dashboard-value-box th {
    text-align: left;
  }
  
  .dashboard-invoice-box td,
  .dashboard-value-box td {
    font-weight: bold;
    font-size: 1.4rem;
  }
  
  /* Mobile Responsive */
  @media (max-width: 768px) {
    .dashboard {
      padding: 1rem;
    }
  
    .dashboard h2 {
      font-size: 2rem;
    }
  }
  